import React, { useState } from 'react';
import '../global.css';
import '../App.css';


function Rodada() {
  const [rodada, setRodada] = useState({ _id: 1, nivel: 1, energia: 1 });
  //const [log,setLog] = useState([rodada]);
  const [escolher, setEscolher] = useState(false);
  const [nivelMovel, setNivelMovel] = useState(1);
  const [nivelConquista, setNivelConquista] = useState(1);

  const [energiaMovel, setEnergiaMovel] = useState(1);
  const [energiaConquista, setEnergiaConquista] = useState(1);


  /*useEffect(() => { 
    setNivelMovel(atual => atual + 1);
  },[nivelConquista]);

  useEffect(() => { 
    setEnergiaMovel( atual => atual + 1);
  },[energiaConquista]);*/
  function AlterMovel(indicador, valor) {

    if (indicador === 'nivel')
      setNivelMovel(atual => (atual + valor < 0 ? 0 : (atual + valor > 10 ? 10 : atual + valor)));
    else
      setEnergiaMovel(atual => (atual + valor < 0 ? 0 : (atual + valor > 10 ? 10 : atual + valor)));

  }

  function AlterConquista(indicador, valor) {

    if (indicador === 'nivel') {

      setNivelConquista(atual => (atual + valor < 0 ? 0 : (atual + valor > 10 ? 10 : atual + valor)));
      nivelConquista > 0 && nivelConquista < 10 &&
        setNivelMovel(atual => (atual + valor < 0 ? 0 : (atual + valor > 10 ? 10 : atual + valor)));
    } else {

      setEnergiaConquista(atual => (atual + valor < 0 ? 0 : (atual + valor > 10 ? 10 : atual + valor)));
      energiaConquista > 0 && energiaConquista < 10 &&
        setEnergiaMovel(atual => (atual + valor < 0 ? 0 : (atual + valor > 10 ? 10 : atual + valor)));
    }

  }

  function newRodada(upNivel, upEnergia) {
    let nivelNew = nivelConquista;
    let energiaNew = energiaConquista;

    if (upNivel)
      nivelNew++;
    if (upEnergia)
      energiaNew++;

    setNivelMovel(nivelNew);
    setNivelConquista(nivelNew);

    setEnergiaMovel(energiaNew);
    setEnergiaConquista(energiaNew);

    setRodada({
      _id: rodada._id + 1,
      nivel: nivelNew,
      energia: energiaNew
    });

    setEscolher(false);
  }

  function RodadaControl() {

    if (rodada._id + 1 <= 2)
      newRodada(true, true);
    else
      setEscolher(true);

  }


  return (
    <>
      <div className="Rodada">
        {escolher
          ? <div className="Rodada-escolha" >
            <button onClick={() => newRodada(true, false)}> + Nivel </button>
            <button onClick={() => newRodada(false, true)}> + Energia </button>
          </div>
          : <button onClick={RodadaControl}> Nova Rodada </button>
        }
        <div className="Rodada-count" >
          <p> {rodada._id} </p>
        </div>
      </div>

      <div className="Reserva" >
        <header>
          <strong> NÃ­vel (N) </strong>
        </header>
        <div className="Reserva-movel">
          <button onClick={() => AlterMovel('nivel', - 1)} > - </button>
          <p> {nivelMovel} </p>
          <button onClick={() => AlterMovel('nivel', 1)}> + </button>
        </div>
        <div className="Reserva-conquista">
          <button onClick={() => AlterConquista('nivel', - 1)} > - </button>
          <p> {nivelConquista} </p>
          <button onClick={() => AlterConquista('nivel', 1)}> + </button>
        </div>
      </div>

      <div className="Reserva" >
        <header>
          <strong> Energia (E) </strong>
        </header>
        <div className="Reserva-movel">
          <button onClick={() => AlterMovel('energia', - 1)} > - </button>
          <p> {energiaMovel} </p>
          <button onClick={() => AlterMovel('energia', 1)}> + </button>
        </div>
        <div className="Reserva-conquista">
          <button onClick={() => AlterConquista('energia', - 1)} > - </button>
          <p> {energiaConquista} </p>
          <button onClick={() => AlterConquista('energia', 1)}> + </button>
        </div>
      </div>
    </>
  )
}

export default Rodada;