import React, { useState, useEffect } from 'react';
import '../global.css';
import '../App.css';
import Rodada from '../components/Rodada';


function Game() {
  const [fortaleza, setFortaleza] = useState(30);
  const [compras, setCompras] = useState([]);

  useEffect(() => {
    setCompras([
      { marco: 20, text: 20, on: false },
      { marco: 10, text: 10, on: false },
      { marco: 5, text: 5, on: false }
    ])

  }, [])

  function alterFortaleza(valor) {
    setFortaleza(atual => (atual + valor < 0 ? 0 : (atual + valor > 30 ? 30 : atual + valor)));

    setCompras(
      compras.map(compra =>
        (compra.marco >= fortaleza + valor && compra.text !== 'OK'
          ? { ...compra, on: true }
          : compra
        )
      )
    );


    // setDatas(
    //   datas.map(item => 
    //       item.id === index 
    //       ? {...item, someProp : "changed"} 
    //       : item 
    //))
  }

  function ConfirmaCompra(compra) {
    /* let newCompras = [...compras];

    newCompras[marco].text = 'OK';

    setCompras(newCompras); */
    compra.on && compra.text !== 'OK' &&
      setCompras(
        compras.map(atual =>
          (atual.marco === compra.marco && atual.on
            ? { ...atual, text: 'OK', on: false }
            : atual
          )
        )
      );



    //setCompras(atual => ([...atual, atual[index]: 'OK']));

/*     setCompras(

      compras.map(compra =>
        compra === index
          ? { ...compra, 25 }
          : compra)
    );

 */  }

  function Aviso() {

    const aviso = compras.filter(compra => compra.on).length;

    return (
      aviso > 0
        ? <p className="Aviso"> Compre {aviso} card{aviso > 1 && 's'}!</p>
        : <p className="Aviso"> <br /> </p>)

  }

  function Compra() {


  }

  //console.log(Object.values(compras));

  return (


    <main className="list-Reserva">
      <Rodada />
      <div className="Fortaleza" >
        <header>
          <strong> Fortaleza </strong>
        </header>
        <div className="Fortaleza-Vida">
          <button onClick={() => alterFortaleza(- 1)} > - </button>
          <p> {fortaleza} </p>
          <button onClick={() => alterFortaleza(1)}> + </button>
        </div>
        {/* {compras.filter(compra => compra.on === true) === true
          ? <> Compre 1 card </>
          : <> teste </>
        } */}
        <Aviso></Aviso>

        <ul className="Compra">
          {compras.map((compra) =>
            <div key={compra.marco}>
              <button onClick={() => ConfirmaCompra(compra)}> {compra.text} </button>
            </div>

          )}

        </ul>


      </div>


    </main >

  )
}

export default Game;